import BScroll from 'better-scroll';
export default {
	data() {
		return {
			y: 0,
			left: [{
				"name": "项目1"
			}, {
				"name": "项目2"
			}, {
				"name": "项目3"
			}, {
				"name": "项目4"
			}, {
				"name": "项目5"
			}, {
				"name": "项目6"
			}, {
				"name": "项目7"
			}, {
				"name": "项目8"
			}, {
				"name": "项目9"
			}, {
				"name": "项目10"
			}],
			right: [{
				"name": "项目1",
				"list": [1, 2, 3]
			}, {
				"name": "项目2",
				"list": [1, 2, 3, 4, 5, 6, 7, 8]
			}, {
				"name": "项目3",
				"list": [1]
			}, {
				"name": "项目4",
				"list": [1, 2, 3, 4, 5]
			}, {
				"name": "项目5",
				"list": [1, 2, 3, 4, 5, 6]
			}, {
				"name": "项目6",
				"list": [1, 2, 3, 4, 5, 6]
			}, {
				"name": "项目7",
				"list": [1, 2, 3, 4, 5, 6]
			}, {
				"name": "项目8",
				"list": [1, 2, 3, 4, 5, 6]
			}, {
				"name": "项目9",
				"list": [1, 2, 3, 4, 5, 6]
			}, {
				"name": "项目10",
				"list": [1, 2, 3, 4, 5, 6]
			}]
		}
	},
	created() {
		console.log("created")

	},
	computed: {

	},
	mounted() {
		console.log("mounted")
		this.y = 0
		this.$nextTick(() => {
			// console.log(this.$refs.right.$el)
			this.int()
			//根据y，top，bottom确定出用户当前滑动到了哪个栏目了
			for (let i = 0; i < this.left.length; i++) {
				this.left[i].top = Math.abs(this.$refs.rightList[i].$el.offsetTop)
				this.left[i].bottom = Math.abs(this.$refs.rightList[i].$el.offsetTop) + Math.abs(this.$refs.rightList[i].$el.offsetHeight)
			}
		})


		
	},
	methods: {
		int() {
			this.iLeft = new BScroll(this.$refs.left.$el, {
				scrollY: true,
				click: true,
				probeType: 3
			})
			this.iRight = new BScroll(this.$refs.right.$el, {
				scrollY: true,
				click: true,
				probeType: 3
			})
			this.iRight.on('scroll', (pos) => {
				this.y = Math.abs(pos.y)
				// console.log("this.y:" + this.y)
				// Math.abs(this.y)
			})

		}
	}
}
